import React from 'react';
 
class Jumbotron extends React.Component {
 
  render() {
 
    return (
 
        <div className="jumbotron">
            <div className="container">
                <h1 className="display-1">Proyectos</h1>
                <p>
                    Mi nombre es Rafael Flores 
                    <br/>
                    a.
                </p>
                <div className="album py-5 bg-light">
				    <div className="container">
 
				      <div className="row">
				        <div className="col-md-4">
				          <div className="card mb-4 shadow-sm">
				            <svg className="bd-placeholder-img card-img-top" width="100%" height="225" xmlns="https://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img" aria-label="Placeholder: Servicio 1"><title>Servicio 1</title><rect width="100%" height="100%" fill="#55595c"/><text x="40%" y="50%" fill="#eceeef" dy=".3em">NODE JS</text></svg>
				            <div className="card-body">
				              <p className="card-text">Programa realizado para las tablitas steak house, en el framework de Nodejs Express.</p>
				              <div className="d-flex justify-content-between align-items-center">
				                <div className="btn-group">
				                  <button type="button" className="btn btn-sm btn-outline-secondary">Leer</button>
				                  
				                </div>
				                
				              </div>
				            </div>
				          </div>
				        </div>
                        </div>
            </div>
        </div>
        </div>
        </div>
        
 
    )
    
  }
 
}
 
export default Jumbotron;